import React, { Component } from 'react'

export default class Checker extends Component {
    constructor(props) {
        super(props);
        this.state = {
                    result: 0,
                    value: '',
                    weight: ''
                    };
    
        this.handleChange = this.handleChange.bind(this);
      }
    
      handleChange(event) {
        this.setState({ [event.target.name]: Number(event.target.value) });
      }

      addAction =(event)=>{
        let x = (this.state.value / this.state.weight) * 1000
        this.setState({result: x})
      }
        
      render() {
        return (
            <div>
                <section className="colorlib-experience" data-section="checker">
                     <div className="colorlib-narrow-content">
                         <div className="row">
                             <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                                 <h2 className="colorlib-heading animate-box">Power to Weight Ratio Calculator</h2>
                                 <span className="heading-meta">Can you beat me</span>
                                 <div className="about-desc">
                                    <table>
                                        <tr>
                                            <th><h3>RS4</h3></th>
                                            <th></th>
                                            <th><h3>Your vehicle</h3></th>
                                        </tr>
                                        <tr>
                                            <th><pre>Power (bhp)   </pre></th>
                                            <th><pre>Weight (kg)              </pre></th>
                                            <th><pre>Power (bhp)   </pre></th>
                                            <th><pre>Weight (kg)</pre></th>
                                        </tr>
                                        <tr>
                                            <td><pre>448    </pre></td>
                                            <td><pre>1650</pre></td>
                                            <td>
                                                <input
                                                    name = "value"
                                                    type="number"
                                                    value={this.state.value} 
                                                    onChange={this.handleChange}
                                                    />
                                            </td>
                                            <td>
                                                <input
                                                    name = "weight"
                                                    type="number"
                                                    value={this.state.weight}
                                                    onChange={this.handleChange}
                                                    />
                                            </td>
                                        </tr>
                                        <tr>
                                            <td></td>
                                            <td><pre>271.52</pre></td>
                                            <td><input type="button" onClick={this.addAction} value="Calculate" /></td>
                                            <td><input type="text" value={this.state.result} readOnly /></td>
                                        </tr>
                                    </table>
                                 </div>
                             </div>
                            </div>
                        </div>
                    </section>
            </div>
        );
    }
}